@import url(https://cdn.fontcdn.ir/Font/Persian/Shabnam/Shabnam.css);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.auth {
	width: 450px;
	max-width: 95%;
	margin: 35px auto;
	border: 1px solid #ccc;
	min-height: 250px;
	text-align: right;
	direction: rtl;
	position: relative;
}

	
label {
	display: block;
	margin: 5px;
}

.login {
    border-left: 1px solid #ccc;
    padding: 0 10px;
}
.validationError {
	color: red;
	display: block;
}
.register {
    padding: 0 10px;
}


.login, .register {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.authFooter {
	display: flex;
	flex-direction: column;
	margin: 25px auto;
}

.logout {
	width: 35px;
	cursor: pointer;
}

.selecContainer {
    margin: 0 auto;
    position: relative;
}
.displayContainer {
	display: flex;
	justify-content: space-between;
	border: 1px solid #999;
	background-color: white;
	min-height: 30px;
	border-radius: 5px;
}
.inputContainer {
    width: 19px;
}
.selectInput {
    width: 19px;
    border: none;
    margin: 0 5px;
}
.selectInput:focus {
	box-shadow: non;
	border: none;
}
.selectBtn {
    width: 25px;
    cursor: pointer;
    font-size: 10px;
    text-align: center;
    padding-top: 8px;
}
.optionContainer {
    position: absolute;
    width: 100%;
    border: 1px solid gold;
    left: 0;
    box-sizing: border-box;
    max-height: 210px;
    overflow-y: auto;
    transition: all 200ms;
    background-color: white;
    z-index: 2;
}
.selectOption {
    height: 30px;
    cursor: pointer;
    margin-bottom: 5px;
    padding: 0 5px;
}
.activeOption,
.selectOption:focus,
.selectOption:hover {
    background-color: gold;
    color: #444;
}
.selectDisplay {
    width: 100%;
    min-height: 20px;
}
.optionHide {
    max-height: 0px;
    z-index: -1;
    border: none;
}
.fab {
	position: fixed;
	bottom: 50px;
	right: 50px;
	width: 60px;
	height: 60px;
	border: 1px solid #aaa;
	border-radius: 50%;
	text-align: center;
	padding-top: 8px;
	box-sizing: border-box;
	font-size: 35px;
	color: black;
	background-color: gold;
	cursor: pointer;
}

.spinner {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: #00000059;
	z-index: 6;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
	color: white;
}

.spinnerIcon {
	height: 40px;
	margin: 0 5px;
}

.loader {
	display: flex;
	align-items: center;
	justify-content: center
}
.modalContainer {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	overflow-y: auto;
	display: flex;
    justify-content: center;
    z-index: 2;
}
.modalBackBox {
	background-color: #1a1a1a78;
	width: 100%;
	height: 100%;
	position: absolute;
}
.modal {
	z-index: 500;
	position: absolute;
	background-color: white;
	width: 600px;
	max-width: 90%;
	margin: 45px auto;
	text-align: right;
	direction: rtl;
	transition: all 250ms;
	scale: 0;
}
.modal.modalOpen {
	scale: 1;
}
.modalHeader {
	background-color: gold;
	padding: 0 15px;
	display: flex;
	justify-content: space-between;
	height: 40px;
	align-items: center;
}

.closeIcon {
	background-color: #444;
	color: gold;
	width: 25px;
	text-align: center;
	border-radius: 30px;
	font-size: 15px;
	height: 25px;
	padding-top: 4px;
	box-sizing: border-box;
	cursor: pointer;
}
.modalBody {
	min-height: 150px;
	padding: 5px 15px;
}

.ModalFooter {
	display: flex;
	justify-content: flex-end;
	margin: 25px 5px;
}

.ModalFooter button {
    margin:  0 10px;
}

.emodalcontainer {
	position: fixed;
	background-color: #00000063;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 9999;
}

.EModal {
	min-width: 350px;
	background-color: white;
	padding: 20px;
	border: 2px solid gold;
	border-radius: 10px;
	max-width: 90%;
	text-align: right;
	direction: rtl;
}
.taskList {
	display: flex;
	min-height: 100vh;
	align-items: first baseline;
	cursor: all-scroll;
}
.taskCard {
	width: 350px;
	border: 1px solid black;
	flex-shrink: 0;
	min-height: 250px;
	margin: 10px;
	text-align: right;
	direction: rtl;
	border-radius: 8px;
	margin-bottom: 80px;
}
.actionBar {
	background-color: #444;
	height: 45px;
	align-items: center;
	display: flex;
	justify-content: space-between;
	padding: 0 20px;
	color: white;
    border-bottom: 4px solid gold;
    border-radius: 8px 8px 0 0;
}
.taskDescription {
	padding: 5px 15px;
	min-height: 80px;
	border-bottom: 1px solid #ccc;
	background-color: #eee;
	font-size: 12px;
}

.addSubTaskBtn {
	width: 30px;
	height: 30px;
	background-color: gold;
	text-align: center;
	color: #444;
	border-radius: 30px;
	cursor: pointer;
	font-size: 19px;
	border: none;
}

.noTask {
	margin: 50px auto;
	border: 2px solid gold;
	padding: 20px 0;
	width: 350px;
	text-align: center;
	border-radius: 15px;
	box-shadow: 0 0 8px 3px #aaa;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 130px;
}

.nosubTask {
	text-align: center;
	color: grey;
	height: 60px;
	padding-top: 30px;
}

.subtask {
	position: relative;
	margin: 10px auto;
	border: 1px solid #ccc;
	padding-bottom: 10px;
	width: 90%;
}

.subTaskactionBar {
	background-color: #888;
	color: white;
	padding: 10px 5px;
	border-bottom: 4px solid gold;
}
.subTaskContent {
	padding: 10px;
	font-size: 13px;
}

.status {
	position: relative;
	display: flex;
	align-items: center;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	font-size: 12px;
}
.status:hover .stausHandler {
	max-height: 350px;
	border: 1px solid #ccc;
}
.stausHandler {
	position: absolute;
	background-color: white;
	width: 120px;
	left: -50%;
	top: 100%;
	padding: 0 5px;
	text-align: center;
	max-height: 0;
	overflow-y: hidden;
	transition: all 250ms;
	z-index: 3;
	border: none;
}


.stausHandler p {
	background-color: royalblue;
	color: white;
	margin: 5px 0;
	padding: 5px;
	font-size: 12px;
	border-radius: 3px;
	cursor: pointer;
}
.txtInput {
	width: 100%;
	min-height: 30px;
	padding: 0 5px;
	box-sizing: border-box;
}

.txtInput:focus {
	border: 1px solid gold;
	box-shadow: 0px 0px 3px gold;
}
.btnGold {
	display: block;
	width: 120px;
	height: 35px;
	background-color: gold;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}

.btnCancel:hover {
    background-color: gold;
}
.btnCancel {
	background-color: white;
	border: 2px solid gold;
	border-radius: 5px;
	width: 60px;
    cursor: pointer;
    transition: all 250ms;
}
.btnLink {
	background-color: white;
	color: lightcoral;
	border-radius: 5px;
	cursor: pointer;
	border: none;
	margin: 10px auto;
}
.icon {
	height: 15px;
	margin-left: 5px;
}
.iconContainer {
    width: 20px;
    margin: 0 3px;
}
.menIcon {
	background-color: #ffd700;
	height: 3px;
	margin: 3px 0;
	border-radius: 3px;
}


.loader {
    color: #ffffff;
    font-size: 20px;
    margin: 100px auto;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: load4 1.3s infinite linear;
    animation: load4 1.3s infinite linear;
    transform: translateZ(0);
  }
  @-webkit-keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  @keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  

  .spIcon {
    text-align: center;
  }
  
  .spIcon > div {
    width: 8px;
    height: 8px;
    background-color: #fff;
  
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .spIcon .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  
  .spIcon .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  
  @-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% {
      transform: scale(0);
    } 40% {
      transform: scale(1.0);
    }
  }
 body {
	font-family: "Shabnam", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;;
}
 .navbar {
	background-color: #444;
	color: white;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
	padding: 0 20px;
}
.navbar h1 {
	font-size: 20px;
}


